import Logger from '~/core/Logger'
import API from '~/core/CoreAPI'
import Brightline from 'brightline.js'
import overlayTpl from './overlay.tpl?raw'
import ChallengeView from '../../ChallengeView'
import $original from 'jquery'

let $

/**
 * Alias for Logger.log(). Prepends class name to funcName.
 *
 * @param originator The name of the instance to prepend to the log message
 * @param funcName The name of the function generating the log message
 * @param message The message to log
 * @param payload Data object
 * @param level Log level (ERROR, WARN, INFO, DEBUG)
 */
var log = function (originator, funcName, message, payload, level) {
  Logger.log(originator + '.' + funcName, message, payload, level)
}

export default class ChallengeOverlayView extends ChallengeView {
  /**
   * Store the last
   */
  lastKeyDown = 0

  /**
   * Pass in instance of jQuery bound to page in which view is being rendered
   *
   * @param jQuery
   */
  setJQuery(jQuery) {
    log(this.originator, 'setJQuery', 'Setting jQuery')
    $ = jQuery || $original
  }

  /**
   * Injects rendered view into jQuery container
   *
   * @param $container jQuery element into which view should be injected
   */
  inject($container) {
    super.inject($container, this.render(overlayTpl))

    var self = this

    API.PubSub.listen('ChallengeModel.text.set', function () {
      $('#challengeSource').html(self.model.getText())
    })
  }

  /**
   * Renders view
   */
  render(tpl) {
    log(this.originator, 'render', 'Rendering view')

    tpl = tpl || ''
    var template = new Brightline(tpl)

    template.set('text', this.model.getText())

    return template.render()
  }

  /**
   * Binds handlers to UI
   */
  bindUIHandlers() {
    log(this.originator, 'bindUIHandlers', 'Binding UI handlers', null, 'TRACE')

    var self = this
    var $challengeText = $('#challengeText')
    var $close = $('#challenge-overlay *[data-close]')
    var $productivityBypass = $('#productivityBypass')

    $close.click(this.onCloseClicked.bind(this))

    $productivityBypass.click(self.model.setProductivityBypass.bind(self.model))

    $challengeText.keydown(this.onKeyDown.bind(this))

    $challengeText.on('input', function () {
      self.onInput($(this).val())
    })
  }

  /**
   * Closes tab when "close" link is clicked
   */
  onCloseClicked() {
    API.Chrome.Tab.getSelected(null, function (tab) {
      API.Chrome.Tab.remove(tab.id)
    })
  }

  /**
   * Updates key counter when key is pressed, if it's not an excluded key
   *
   * @param e keydown event
   * @returns {boolean}
   */
  onKeyDown(e) {
    this.lastKeyDown = e.keyCode
  }

  onInput(inputText) {
    if (inputText.length - this.model.getKeyCounter() > 1) {
      alert(API.Chrome.Translation.get('numberOfKeysDoesNotMatch'))
      this.resetChallenge()
      return false
    }

    this.model.updateKeyCounter(inputText.length)

    if (!this.model.isCorrect(inputText, this.lastKeyDown === 229 ? -1 : 0)) {
      alert(API.Chrome.Translation.get('madeAMistake'))

      this.resetChallenge()

      return false
    }

    if (this.model.isComplete(inputText)) {
      $.modal.close()
      this.resetChallenge()
      API.PubSub.publish('Challenge.complete')
    }
  }

  /**
   * Clears the challenge text box, and resets the key counter
   */
  resetChallenge() {
    log(this.originator, 'resetChallenge', 'Resetting challenge')

    var $challengeText = $('#challengeText')

    $challengeText.val('')
    $challengeText.focus()

    this.model.resetKeyCounter()
  }
}
