import Logger from '~/core/Logger'
import API from '~/core/CoreAPI'

/**
 * Alias for Logger.log(). Prepends class name to funcName.
 *
 * @param originator The name of the instance to prepend to the log message
 * @param funcName The name of the function generating the log message
 * @param message The message to log
 * @param payload Data object
 * @param level Log level (ERROR, WARN, INFO, DEBUG)
 */
var log = function (originator, funcName, message, payload, level) {
  Logger.log(originator + '.' + funcName, message, payload, level)
}

export default class ActivityMonitorController {
  view = null
  model = null
  timer = null

  /**
   * Initializes controller
   */
  init() {
    log(this.originator, 'init', 'Initializing controller', null, 'DEBUG')

    this.model.init()
    this.addListeners()

    API.PubSub.publish('ActivityMonitor.component.initialized')

    this.view.bindActivityDetectors()

    // TODO: I really want to do this, but can't because we don't know whether tab is blockable here
    //            if (this.model.isBlockable() && !this.model.isDisabled()){
    //                this.activate();
    //            }
  }

  /**
   * Activates ActivityMonitor
   */
  activate() {
    log(this.originator, 'activate', 'Activating ActivityMonitor')

    this.view.bindActivityDetectors()
    this.startTimer()
  }

  /**
   * Adds message listeners
   */
  addListeners() {
    log(this.originator, 'addListeners', 'Adding listeners', null, 'DEBUG')

    var self = this

    API.PubSub.listen('ActivityMonitor.overlay.shown', function () {
      self.stopTimer()
    })

    API.PubSub.listen('ActivityMonitor.overlay.hidden', function () {
      self.resetTimer()
      self.startTimer()
    })

    API.PubSub.listen('ActivityMonitor.activity.detected', function () {
      self.resetTimer()
    })

    API.PubSub.listen('*.tab.selected', function (message, payload) {
      self.onTabSelected(payload.tab, payload.blockable)
    })

    API.PubSub.listen('*.tab.activity', function (message, payload) {
      if (self.model.blockable && payload.tab?.audible) {
        self.resetTimer()
      }
    })

    API.PubSub.listen('*.page.killed', function (message, payload) {
      self.killPage(payload.redirectURL)
    })
  }

  /**
   * When tab is selected, stops and resets timer, hides overlay, then
   * restarts timer, if applicable.
   *
   * @param selectedTab
   * @param blockable
   */
  onTabSelected(selectedTab, blockable) {
    log(
      this.originator,
      'onTabSelected',
      'New tab selected',
      {
        selectedTab: selectedTab,
        blockable: blockable,
      },
      'TRACE',
    )

    /* First, stop timer for tab that was selected previously */
    if (this.model.currentTabID !== null) {
      this.stopTimer()
    }

    /* Next, set newly selected tab as the current tab */
    this.model.currentTabID = selectedTab.id
    this.model.currentURL = selectedTab.url
    this.model.blockable = blockable

    /* Reset the ActivityMonitor timer, since switching tabs means the user is active */
    this.resetTimer()
    this.view.hide()

    /* If new tab is a blacklisted url, start the ActivityMonitor timer */
    if (blockable && !this.model.isDisabled()) {
      this.startTimer()
    }
  }

  /**
   * Starts the timer
   */
  startTimer() {
    log(
      this.originator,
      'startTimer',
      'Starting timer with ' + this.model.interval + '-second interval',
    )

    clearInterval(this.timer)

    this.timer = setInterval(this.tick.bind(this), this.model.interval * 1000)
  }

  /**
   * Stops the timer
   */
  stopTimer() {
    log(this.originator, 'stopTimer', 'Stopping timer')

    clearInterval(this.timer)

    this.timer = null
  }

  /**
   * Resets the timer to zero
   */
  resetTimer() {
    log(this.originator, 'resetTimer', 'Resetting timer', null, 'TRACE')

    this.model.elapsedTime = 0
  }

  /**
   * Kills page by redirecting to redirectURL
   *
   * @param redirectURL The url to which the page should be redirected
   */
  killPage(redirectURL) {
    API.Analytics.event('BLOCK_PAGE', {
      isNuclear: API.NuclearOption.isActive(),
      hostname: window.location.hostname,
    })

    log(this.originator, 'killPage', 'Killing page', redirectURL, 'INFO')

    top.location.href =
      redirectURL +
      '?content&customMsg=' +
      API.Settings.get('customStayFocusdMsg')
  }

  /**
   * Increments elapsedTime counter
   */
  tick() {
    this.model.elapsedTime += this.model.interval

    log(
      this.originator,
      'tick',
      'Elapsed time: ' + this.model.elapsedTime + ' secs',
      null,
      'DEBUG',
    )

    if (this.model.isMaxInactiveTimeExceeded()) {
      log(
        this.originator,
        'tick',
        'Max inactive time exceeded: ',
        this.model.maxInactiveTime,
        'INFO',
      )

      this.view.show()
    }
  }
}
