import $ from 'jquery'
import SmartBomb from './SmartBomb'
import API from '~/core/CoreAPI'
import { initPubSubForwarder } from '~/utils/initPubSubForwarder.js'
import '~/assets/tailwind.css'
import { Component } from './ComponentFactory'
import { injectComponentFactory } from '~/utils/component-factory'

async function initContent() {
  // Only run in the top frame
  await API.Settings.initAsync()
  const componentFactory = injectComponentFactory()
  API.Bugsnag.init('content')

  initPubSubForwarder()

  $(document).ready(function () {
    SmartBomb.init()

    componentFactory.load({
      name: 'ActivityMonitorOverlay',
      instance: 'activityMonitor',
      view: 'overlay',

      onLoaded: function (component: Component) {
        component.controller.init()
      },
    })

    componentFactory.load({
      name: 'OnboardingOverlay',
      instance: 'onboardingOverlay',

      onLoaded(component: Component) {
        component.controller.init()
      },
    })

    componentFactory.load({
      name: 'BlockedPageOnboardingRequiredOverlay',
      instance: 'blockedPageOnboardingRequiredOverlay',

      onLoaded(component: Component) {
        component.controller.init()
      },
    })
  })

  if (location.href.startsWith('https://www.stayfocusd.com/blocked')) {
    const Controller = (
      await import(
        '~/components/UsageSummaryOverlay/UsageSummaryOverlayController'
      )
    ).default
    const controller = new Controller()
    await controller.init()
  }
}

if (API.Utils.isTopFrame()) {
  initContent()
}
