import API from '~/core/CoreAPI'
import Browser from 'webextension-polyfill'

var log = API.Utils.createScopedLog('BlockedPageOnboardingRequiredOverlayView')

export default class BlockedPageOnboardingRequiredOverlayView {
  iframe = null

  constructor() {
    log('constructor', 'Constructing view', null, 'DEBUG')
  }

  init() {
    log('init', 'Initializing view', null, 'DEBUG')

    // Create the URL
    const url = new URL(
      Browser.runtime.getURL(
        '/components/BlockedPageOnboardingRequired/onboarding-required.html',
      ),
    )
    const existing = document.querySelector(`iframe[src="${url}"]`)
    if (existing) {
      log('init', 'View already initialized', null, 'DEBUG')
      return
    }

    // Create the iframe
    const iframe = document.createElement('iframe')
    iframe.src = url

    iframe.style.zIndex = 9999
    iframe.style.width = '423px'
    iframe.style.height = '292px' // enough for 3 lines of text
    iframe.style.right = 0
    iframe.style.top = 0
    iframe.style.position = 'fixed'
    iframe.style.backgroundColor = 'transparent'
    iframe.setAttribute('allowtransparency', 'true')
    iframe.setAttribute('frameBorder', '0')

    document.body.append(iframe)
    this.iframe = iframe

    void API.Analytics.event('SHOW_BLOCKED_PAGE_ACTION_REQUIRED')
  }

  remove() {
    this.iframe.remove()
  }
}
