import Browser from 'webextension-polyfill'

export default class UsageSummaryOverlayView {
  constructor() {}

  init() {
    // Create the URL
    const url = new URL(
      Browser.runtime.getURL('/components/UsageSummary/base.html'),
    )
    // Create the iframe
    const iframe = document.createElement('iframe')
    iframe.id = 'uageSummaryiFrame'
    iframe.src = url
    iframe.style.width = '100%'
    iframe.style.height = '562px'
    iframe.style.border = 'none'
    iframe.style.background = 'rgb(255 255 255 / 50%)'
    iframe.style.boxShadow = '0 3px 8px rgba(0, 0, 0, 0.24)'
    iframe.style.borderRadius = '14px'

    // Mount the iframe
    const block = document.getElementById('msg')
    if (block != undefined) block.insertAdjacentElement('afterend', iframe)
    else document.body.appendChild(iframe)
  }
}
