import Logger from '~/core/Logger'
import API from '~/core/CoreAPI'
import Brightline from 'brightline.js'
import optionsTpl from './options.tpl?raw'
import ChallengeView from '../../ChallengeView'
import $original from 'jquery'

let $
/**
 * Alias for Logger.log(). Prepends class name to funcName.
 *
 * @param originator The name of the instance to prepend to the log message
 * @param funcName The name of the function generating the log message
 * @param message The message to log
 * @param payload Data object
 * @param level Log level (ERROR, WARN, INFO, DEBUG)
 */
var log = function (originator, funcName, message, payload, level) {
  Logger.log(originator + '.' + funcName, message, payload, level)
}

export default class ChallengeOptionsView extends ChallengeView {
  /**
   * Pass in instance of jQuery bound to page in which view is being rendered
   *
   * @param jQuery
   */
  setJQuery(jQuery) {
    log(this.originator, 'setJQuery', 'Setting jQuery')
    $ = jQuery || $original
  }

  /**
   * Injects rendered view into jQuery container
   *
   * @param $container jQuery element into which view should be injected
   */
  inject($container) {
    super.inject($container, this.render(optionsTpl))
  }

  /**
   * Renders view
   */
  render(tpl) {
    log(this.originator, 'render', 'Rendering view')

    tpl = tpl || ''
    var template = new Brightline(tpl)

    if (this.model.isRequired()) {
      template.set('checked', 'checked="checked"')
    }

    template.set('customChallenge', this.model.getText())
    template.set('minChallengeTextLength', this.model.minLength)

    return template.render()
  }

  /**
   * Binds handlers to UI
   */
  bindUIHandlers() {
    log(this.originator, 'bindUIHandlers', 'Binding UI handlers', null, 'TRACE')

    var self = this
    var $customChallenge = $('#customChallenge')

    $('input[name=setCustomChallenge]').click(function () {
      self.model.setText($customChallenge.val())
    })

    $('#resetCustomChallenge').click(function () {
      self.model.resetText()

      $customChallenge.val(self.model.getText(true))
    })

    $('#requireChallengeCheckbox').click(function () {
      self.setChallengeRequired($(this))
    })
  }

  /**
   * Sets challengeRequired option
   *
   * @param checkbox The jquery object for the challengeRequired checkbox
   */
  setChallengeRequired(checkbox) {
    log(
      this.originator,
      'setChallengeRequired',
      'Setting challenge required',
      checkbox.prop('checked') ? 'ON' : 'OFF',
    )

    if (checkbox.prop('checked')) {
      var confirmed = confirm(
        API.Chrome.Translation.get('confirmRequireChallenge'),
      )

      if (confirmed) {
        this.model.setRequired(true)
      } else {
        checkbox.prop('checked', false)
      }
    } else {
      this.model.setRequired(false)
    }
  }
}
