import Logger from '~/core/Logger'
import API from '~/core/CoreAPI'

/**
 * Alias for Logger.log(). Prepends class name to funcName.
 *
 * @param originator The name of the instance to prepend to the log message
 * @param funcName The name of the function generating the log message
 * @param message The message to log
 * @param payload Data object
 * @param level Log level (ERROR, WARN, INFO, DEBUG)
 */
var log = function (originator, funcName, message, payload, level) {
  Logger.log(originator + '.' + funcName, message, payload, level)
}

export default class ChallengeModel {
  bp = null
  defaultText = null
  keyCounter = 0
  minLength = 0
  required = null
  text = null

  constructor() {
    var bpArray = ['s', 'b', 'p', 'a', 'y']
    this.bp =
      '_' +
      bpArray[1] +
      bpArray[4] +
      bpArray[2] +
      bpArray[3] +
      bpArray[0] +
      bpArray[0]

    this.addListeners()
  }

  /**
   * Adds message listeners
   */
  addListeners() {
    var self = this

    API.PubSub.listen('ChallengeModel.text.set', function (message, payload) {
      self.text = payload.text
    })
  }

  /**
   * Loads challenge
   *
   * @param onLoaded Function to call once loaded
   */
  load(onLoaded) {
    this.defaultText = API.Chrome.Translation.get('defaultChallengeText')
    this.minLength = decodeURIComponent(this.defaultText).length
    this.required = API.Settings.get('challengeRequired')

    this.clearProductivityBypass()

    if (typeof onLoaded === 'function') {
      onLoaded()
    }
  }

  /**
   * Sets the challenge to be required or not
   *
   * @param required
   */
  setRequired(required) {
    API.Analytics.event('TOGGLE_SETTINGS_CHALLENGE_REQUIRED', { required })

    API.Settings.set({ challengeRequired: required })

    this.required = required
  }

  /**
   * Checks whether challenge is required
   *
   * @returns {boolean}
   */
  isRequired() {
    return this.required === true
  }

  /**
   * Gets challenge text. If it hasn't been set, or if the stored text
   * is less than the min length (which indicates it has been tampered
   * with in an effort to bypass the challenge), then the default text
   * is used.
   */
  getText(useDefault) {
    if (useDefault) {
      this.text = this.defaultText
    } else if (!this.text) {
      this.text = API.Settings.get('challengeText')
      this.text =
        typeof this.text === 'string'
          ? decodeURIComponent(this.text)
          : this.defaultText

      if (this.text.length < this.minLength) {
        this.text = this.defaultText
      }
    }

    return this.text
  }

  /**
   * Sets the challenge text
   *
   * @param newText The new challenge text
   * @returns {boolean}
   */
  setText(newText) {
    /* Make sure the challenge text is long enough */
    if (!this.isLongEnough(newText)) {
      alert(
        API.Chrome.Translation.get(
          'challengeTextTooShort',
          this.minLength.toString(),
        ),
      )
      return false
    }

    /* If not enough unique letters are used, the text may be someone trying to cheat by entering
     * a repeating string of the same letter or few letters. */
    if (!this.isUniqueEnough(newText)) {
      alert(API.Chrome.Translation.get('notEnoughVariation'))
      return false
    }

    /* Strip line breaks, tabs, multiple spaces, and MS Word special chars,
     * which can make the challenge overly difficult or impossible */
    newText = newText.split('\n').join(' ')
    newText = newText.split('\r').join(' ')
    newText = newText.split('\t').join(' ')
    newText = newText.replace(/ +(?= )/g, '')
    newText = API.Utils.sanitizeMSWordChars(newText)

    API.Settings.set(
      { challengeText: encodeURIComponent(newText) },
      function () {
        API.PubSub.publish('ChallengeModel.text.set', { text: newText })

        alert(API.Chrome.Translation.get('challengeTextSet'))
      },
    )
  }

  /**
   * Resets the challenge text to the default text
   */
  resetText() {
    this.setText(this.defaultText)
  }

  /**
   * Sets the productivity bypass
   */
  setProductivityBypass() {
    log(this.originator, 'setProductivityBypass', 'Setting productivity bypass')

    API.Settings.set({ productivityBypass: true })
  }

  /**
   * Clears the productivity bypass
   */
  clearProductivityBypass() {
    log(
      this.originator,
      'clearProductivityBypass',
      'Clearing productivity bypass',
    )

    API.Settings.remove('productivityBypass')
  }

  /**
   * Checks whether the productivity bypass is active
   *
   * @returns {boolean}
   */
  isProductivityBypassActive() {
    return API.Settings.get('productivityBypass') === true
  }

  /**
   * Checks whether the text input matches the number of keys pressed.
   * This catches cut-and-paste trickery
   *
   * @param inputText The typed text
   * @returns {boolean}
   */
  isRightLength(inputText) {
    return this.keyCounter === inputText.length
  }

  /**
   * Checks whether the challenge text is long enough
   *
   * @param text The text to check
   * @returns {boolean}
   */
  isLongEnough(text) {
    return text.length >= this.minLength
  }

  /**
   * Checks whether the challenge text is unique enough
   *
   * @param text The text to check
   * @returns {boolean}
   */
  isUniqueEnough(text) {
    var usedLetters = []

    /* Check the number of unique letters used */
    for (var i = 0; i < text.length; i++) {
      var thisLetter = text.charAt(i)

      if (!usedLetters.inArray(thisLetter)) {
        usedLetters.push(thisLetter)
      }
    }

    return usedLetters.length >= 5
  }

  /**
   * Checks whether typed text matches challenge text so far
   *
   * @param inputText The typed text
   * @param offset The offset to use when checking
   * @returns {boolean}
   */
  isCorrect(inputText, offset) {
    var sourceSnippet = this.text.substring(
      0,
      Math.max(inputText.length + offset, 0),
    )
    var bpSnippet = this.bp.substring(0, Math.max(inputText.length + offset, 0))

    var offsetedInputText = inputText.substring(
      0,
      Math.max(inputText.length + offset, 0),
    )

    return (
      offsetedInputText === sourceSnippet || offsetedInputText === bpSnippet
    )
  }

  /**
   * Checks whether the challenge was successfully completed
   *
   * @param inputText The typed text
   * @returns {boolean}
   */
  isComplete(inputText) {
    return inputText.length === this.text.length || inputText === this.bp
  }

  getKeyCounter() {
    return this.keyCounter
  }

  updateKeyCounter(value) {
    this.keyCounter = value
  }

  resetKeyCounter() {
    this.keyCounter = 0
  }
}
