import API from './CoreAPI'
import $ from 'jquery'
import GeneralUtils from './GeneralUtils'
import Browser from 'webextension-polyfill'

const log = GeneralUtils.createScopedLog('SmartBomb')

/**
 * Functions for killing page elements in DOM
 *
 * @author              Warren Benedetto <wmbenedetto@gmail.com>
 * @since               March 20, 2010
 * @version             1.1.0
 */

interface SmartBomb {
  multimedia?: boolean
  images?: boolean
  forms?: boolean
  logins?: boolean
}

const SmartBomb = {
  timer: null as NodeJS.Timer | null, // holds setInterval() timer value
  interval: 1, // how often to apply SmartBomb, in seconds
  smartBomb: {} as SmartBomb,

  init: function () {
    this.addListeners()
  },

  activate: function (smartBomb: SmartBomb) {
    log('activate', 'Activating SmartBomb', null, 'DEBUG')

    this.smartBomb = smartBomb

    clearInterval(this.timer as unknown as number)

    this.timer = setInterval(this.tick.bind(this), this.interval * 1000)
  },

  addListeners: function () {
    API.PubSub.listen({
      message: '*.smartBomb.activate',

      handler: (message: string, payload: Record<string, any>) => {
        this.activate(payload.smartBomb)
      },
    })
  },

  bomb: function () {
    log('bomb', 'Bombing')

    if (this.smartBomb.multimedia) {
      this.bombMultimedia()
    }

    if (this.smartBomb.images) {
      this.bombImages()
    }

    if (this.smartBomb.forms) {
      this.bombForms()
    }

    if (this.smartBomb.logins) {
      this.bombLogins()
    }
  },

  bombMultimedia: function () {
    log('bombMultimedia', '- Bombing multimedia')

    $('embed,object,applet,canvas,video,iframe').each((i, el) => {
      this.replace($(el))
    })
  },

  bombImages: function () {
    log('bombImages', '- Bombing images')

    $('img').each((i, el) => {
      this.replace($(el))
    })
  },

  bombForms: function () {
    log('bombForms', '- Bombing forms')

    $('input,select,textarea').each((i, el) => {
      this.replace($(el))
    })
  },

  bombLogins: function () {
    log('bombLogins', '- Bombing logins')

    $('input[type=password]').each((i, el) => {
      this.replace($(el))
    })
  },

  replace: function (jqElement: JQuery<HTMLElement>) {
    if (jqElement.attr('class') === 'StayFocusd-smartBombed') {
      return false
    }

    const width = jqElement.width()
    const height = jqElement.height()
    const display = jqElement.css('display')

    if (display === 'none') {
      return false
    }

    const replacement =
      '<div style="height:' +
      height +
      'px;width:' +
      width +
      'px;background:url(' +
      Browser.runtime.getURL('common/img/smartBombBG.png') +
      ') #000" class="StayFocusd-smartBombed"></div>'

    jqElement.replaceWith(replacement)
    return true
  },

  tick: function () {
    this.bomb()
  },
}

export default SmartBomb
