import Logger from '~/core/Logger'
import Brightline from 'brightline.js'
import API from '~/core/CoreAPI'
import overlayTpl from './overlay.tpl?raw'
import $original from 'jquery'
import { createIsolatedElement } from '@webext-core/isolated-element'
import Browser from 'webextension-polyfill'
let $ = $original

/**
 * Alias for Logger.log(). Prepends class name to funcName.
 *
 * @param originator The name of the instance to prepend to the log message
 * @param funcName The name of the function generating the log message
 * @param message The message to log
 * @param payload Data object
 * @param level Log level (ERROR, WARN, INFO, DEBUG)
 */
var log = function (originator, funcName, message, payload, level) {
  Logger.log(originator + '.' + funcName, message, payload, level)
}

export default class ActivityMonitorOverlayView {
  model = null
  isOverlayActive = false
  isOverlayHTMLInjected = false
  customElement = null

  /**
   * Renders view template
   */
  render() {
    log(this.originator, 'render', 'Rendering view', null, 'DEBUG')

    var template = new Brightline(overlayTpl)

    template.set('extensionURL', Browser.runtime.getURL('/'))
    template.set(
      'activityMonitorOverlayHeader',
      API.Chrome.Translation.get('activityMonitorOverlayHeader'),
    )
    template.set(
      'activityMonitorOverlayBody',
      API.Chrome.Translation.get('activityMonitorOverlayBody'),
    )

    for (var i = 1; i <= 4; i++) {
      template.set(
        'activityMonitorOverlayLink' + i,
        API.Chrome.Translation.get('activityMonitorOverlayLink' + i),
      )
    }

    return template.render()
  }

  /**
   * Injects rendered view into page
   */
  async inject(onComplete) {
    log(this.originator, 'inject', 'Injecting ActivityMonitor overlay')
    // :root doesn't work in shadow dom, so fetch the css and replace :root with html
    let textContent = await fetch(Browser.runtime.getURL('style.css')).then(
      (res) => res.text(),
    )
    textContent = textContent.replace(/:root/g, 'html')

    this.customElement = await createIsolatedElement({
      name: 'stayfocusd-still-there',
      css: { textContent },
      mode: 'open',
    })
    document.body.appendChild(this.customElement.parentElement)
    $(this.customElement.isolatedElement)
      .addClass(
        'fixed flex flex-col items-center justify-center top-0 bottom-0 right-0 left-0 bg-neutral hidden',
      )
      .css('z-index', '999999999')
      .append(this.render())

    this.isOverlayHTMLInjected = true

    this.bindUIHandlers()

    if (typeof onComplete === 'function') {
      onComplete()
    }
  }

  /**
   * Binds handlers to UI
   */
  bindUIHandlers() {
    log(this.originator, 'bindUIHandlers', 'Binding UI handlers', null, 'TRACE')

    var self = this

    $(this.customElement.isolatedElement).on(
      'click',
      '#StayFocusd-unblock-link',
      function () {
        if (self.isOverlayActive === true) {
          self.hide()
        }
      },
    )

    /* If key is pressed while screen is blocked, unblock screen */
    $('body').keydown(function () {
      if (self.isOverlayActive === true) {
        self.hide()
      }
    })
  }

  /**
   * Shows ActivityMonitor overlay
   */
  show() {
    var self = this

    /* If the Overlay HTML isn't injected yet, inject it then re-call
     * this function once injection is complete */
    if (!this.isOverlayHTMLInjected) {
      this.inject(function () {
        self.show()
      })

      return null
    }

    log(this.originator, 'show', 'Showing ActivityMonitor overlay')

    /* Hide embedded objects like Flash video and ads */
    $('embed').addClass('StayFocusd-hidden')
    $('object').addClass('StayFocusd-hidden')
    $('applet').addClass('StayFocusd-hidden')

    /* Position and show overlay */
    $(this.customElement.isolatedElement).removeClass('hidden')

    this.isOverlayActive = true

    API.PubSub.publish('ActivityMonitor.overlay.shown')
  }

  /**
   * Hides ActivityMonitor overlay
   */
  hide() {
    if (this.isOverlayActive) {
      log(this.originator, 'hide', 'Hiding ActivityMonitor overlay')

      $(this.customElement.isolatedElement).addClass('hidden')

      /* Show embedded objects like Flash video and ads */
      $('embed').removeClass('StayFocusd-hidden')
      $('object').removeClass('StayFocusd-hidden')
      $('applet').removeClass('StayFocusd-hidden')

      this.isOverlayActive = false

      API.PubSub.publish('ActivityMonitor.overlay.hidden')
    }
  }

  /**
   * Binds functions to detect activity in page
   */
  bindActivityDetectors() {
    log(
      this.originator,
      'bindActivityDetectors',
      'Binding activity detectors',
      null,
      'DEBUG',
    )

    /* Bind timer reset to main page body */
    this.bindTimerReset('body')

    var self = this

    /**
     * Bind timer reset to the bodies of any pages in iframes.
     * This is a fix for sites like Gmail, where the mouse events aren't
     * tracked by the main page, because the whole thing is covered with
     * iframes.
     */
    $('iframe').each(function () {
      var iframeSrc = $(this).attr('src')

      if (typeof iframeSrc !== 'undefined') {
        var iframeBase = API.Domain.extractFullDomain(iframeSrc)
        var pageBase = API.Domain.extractFullDomain(top.location.href)

        /* Only bind to iframes from the same domain (i.e. not ad frames) to avoid
         * cross-domain access violations */
        if (iframeBase === pageBase) {
          self.bindTimerReset($(this).contents().find('body'))
        }
      }
    })
  }

  /**
   * Binds resetTimer method to mouse and keypress events
   * on an element
   *
   * @param element element The element to bind, i.e. $('body')
   */
  bindTimerReset(element) {
    log(
      this.originator,
      'bindTimerReset',
      'Binding timer reset',
      element,
      'TRACE',
    )

    var activityDetectedEvent = 'ActivityMonitor.activity.detected'

    if (element) {
      $(element).mousedown(function () {
        API.PubSub.publish(activityDetectedEvent)
      })

      $(element).mousemove(function () {
        API.PubSub.publish(activityDetectedEvent)
      })

      $(element).keypress(function () {
        API.PubSub.publish(activityDetectedEvent)
      })
    }
  }

  /**
   * Pass in instance of jQuery bound to page in which view is being rendered
   *
   * @param jQuery
   */
  setJQuery(jQuery) {
    $ = jQuery || $original
    log(this.originator, 'setJQuery', 'Setting jQuery')
  }
}
