export type ClassType = 'Model' | 'View' | 'Controller'
export default class Component {
  name: null | string = null
  Model: null | any = null
  View: null | any = null
  Controller: null | any = null

  constructor(name: string, initObj: any) {
    this.name = name
    this.Model = this.decorateClass(initObj.Model)
    this.View = this.decorateClass(initObj.View)
    this.Controller = this.decorateClass(initObj.Controller)
  }

  /**
   * Creates an instance of the specified class type
   *
   * @param classType The type of class instance to create
   * @param instanceName The name of the instance
   * @returns {} Instance of specified class
   */
  create(classType: ClassType, instanceName: string) {
    const allowedTypes = {
      Model: null,
      View: null,
      Controller: null,
    }

    if (!(classType in allowedTypes)) {
      throw new Error(
        '[Component.create] ' + classType + ' is not a valid class type',
      )
    }

    if (!this[classType]) {
      throw new Error(
        '[Component.create] The ' +
          this.name +
          ' Component does not support the ' +
          classType +
          'class',
      )
    }

    return this.decorateInstance(new this[classType](), classType, instanceName)
  }

  /**
   * Decorates class with additional getters for metadata
   *
   * @param classObj The class to decorate
   * @returns {null} Decorated class
   */
  decorateClass(classObj: any) {
    classObj = classObj || null

    if (classObj) {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const self = this

      classObj.prototype.component = self

      classObj.prototype.getComponent = function getComponent() {
        return self
      }

      classObj.prototype.getComponentName = function getComponentName() {
        return this.__meta.componentName
      }

      classObj.prototype.getClassName = function getClassName() {
        return this.__meta.className
      }

      classObj.prototype.getInstanceName = function getInstanceName() {
        return this.__meta.instanceName
      }

      classObj.prototype.getInstanceID = function getInstanceID() {
        return this.__meta.instanceID
      }

      classObj.prototype.getOriginator = function getOriginator() {
        return this.__meta.originator
      }
    }

    return classObj
  }

  /**
   * Adds instance metadata to instance
   *
   * @param instance The instance to decorate
   * @param classType The instance's class type
   * @param instanceName The name of the instance
   * @returns {*}
   */
  decorateInstance(instance: any, classType: ClassType, instanceName: string) {
    const className = this.name + classType

    instance.__meta = {
      componentName: this.name,
      className: className,
      instanceName: instanceName,
      instanceID: className + ':' + instanceName,
      originator: instanceName + ': ' + this.name + classType,
    }

    instance.originator = instanceName + ': ' + className

    return instance
  }
}
