import Logger from '~/core/Logger'
import API from '~/core/CoreAPI'

/**
 * Alias for Logger.log(). Prepends class name to funcName.
 *
 * @param originator The name of the instance to prepend to the log message
 * @param funcName The name of the function generating the log message
 * @param message The message to log
 * @param payload Data object
 * @param level Log level (ERROR, WARN, INFO, DEBUG)
 */
var log = function (originator, funcName, message, payload, level) {
  Logger.log(originator + '.' + funcName, message, payload, level)
}

export default class ActivityMonitorModel {
  DEBUG = false

  interval = 10 // how often to check for activity, in seconds
  elapsedTime = 0 // period of inactivity, in seconds
  maxInactiveTime = 300 // time after which "still there?" prompt is shown, in seconds
  blockable = null
  disabled = null
  currentTabID = null
  currentURL = null

  /**
   * Initializes model
   */
  init() {
    if (API.Settings.get('debugActivityMonitor') === true) {
      this.DEBUG = true
    }

    log(this.originator, 'init', 'Initializing model', null, 'DEBUG')

    if (this.DEBUG) {
      this.interval = 1
      this.maxInactiveTime = 3
    }
  }

  /**
   * Checks whether the maximum inactive time has been exceeded
   */
  isMaxInactiveTimeExceeded() {
    return this.elapsedTime >= this.maxInactiveTime
  }

  /**
   * Checks whether the selected tab is blockable
   */
  isBlockable() {
    return this.blockable === true
  }

  /**
   * Checks whether the ActivityMonitor is disabled
   */
  isDisabled() {
    if (this.disabled === null) {
      this.disabled = API.Settings.get('disableActivityMonitor') === true
    }

    return this.disabled
  }

  /**
   * Toggles the ActivityMonitor's disabled flag to its boolean opposite
   */
  toggle() {
    var disabled = this.isDisabled()
    var action = disabled ? 'Enabling' : 'Disabling'

    log(this.originator, 'toggle', action + ' activity monitor')

    this.disabled = !disabled

    API.Settings.set({ disableActivityMonitor: this.disabled })
  }
}
