import API from '~/core/CoreAPI'
import View from '~/components/UsageSummaryOverlay/UsageSummaryOverlayView'
import Model from '~/components/UsageSummaryOverlay/UsageSummaryOverlayModel'

export default class UsageSummaryOverlayController {
  view = null
  model = null

  constructor() {
    this.model = new Model()
    this.view = new View()
  }

  async init() {
    this.mountView()
  }

  async mountView() {
    if (!this.isOptionsPage() && !this.isBlockedPage()) return

    this.view.init()
  }

  isExtensionPage() {
    return location.hostname === API.Chrome.Extension.getID()
  }
  isOptionsPage() {
    return this.isExtensionPage() && location.pathname === '/options.html'
  }
  isBlockedPage() {
    return location.hostname.endsWith('stayfocusd.com')
  }
}
