import API from '~/core/CoreAPI'
import { MINUTE } from '~/core/Time'
import View from './BlockedPageOnboardingRequiredOverlayView'
import Model from './BlockedPageOnboardingRequiredOverlayModel'

var log = API.Utils.createScopedLog(
  'BlockedPageOnboardingRequiredOverlayController',
)

export default class BlockedPageOnboardingRequiredOverlayController {
  view = null
  model = null

  dismissTimeoutStorageKey = 'action-required-overlay-dismiss-timeout'

  constructor() {
    log('constructor', 'Constructing controller', null, 'DEBUG')

    this.model = new Model()
    this.view = new View()
  }

  async init() {
    log('init', 'Initializing controller', null, 'DEBUG')

    await Promise.all([
      API.Settings.initAsync(),
      API.Requirements.init(),
      API.Analytics.init(),
    ])

    this.addListener()
  }

  async mountView() {
    log('mountView', 'Mounting view', null, 'DEBUG')

    // Storage doesn't update when the user completes onboarding, so we shouldn't show it again until the page reloads
    if (this.model.hasBeenShown) return

    // Don't show the overlay to new users, only existing users
    const isNewUser = API.Settings.get('isNewUser')
    if (isNewUser) return

    // Don't show the overlay immediately after being dismissed
    const dismissTimeout = await API.Storage.get(this.dismissTimeoutStorageKey)
    if (dismissTimeout != null && dismissTimeout >= Date.now()) return

    // Don't show the overlay to users who have already completed onboarding
    const isCompleted = await API.Requirements.isOnboardingCompleted()
    if (isCompleted) return

    this.view.init()
    this.model.hasBeenShown = true
  }

  addListener() {
    API.PubSub.listen('StayFocusd.timer.updated', () => {
      this.mountView()
    })

    window.addEventListener('message', async (event) => {
      if (event.data !== 'BlockedPageOnboardingRequired.dismiss') return

      const showAgainAt = Date.now() + 5 * MINUTE
      await API.Storage.set({
        [this.dismissTimeoutStorageKey]: showAgainAt,
      })
      this.view.remove()
    })
  }
}
